.woocommerce {
  button.button, a.button {
    &--notice {
      background: #AB47BC;
      color: #fff;
      border: 1px solid #AB47BC;
      box-shadow: 0 1px 2px rgba(16, 24, 40, .05);
      border-radius: 8px;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      height: 48px;
      &:hover {
        background-color: #b363c1 !important;
        color:#fff!important;
      }
    }
    &--cart {
      display: inline-flex;
      font-size: 14px;
      flex-shrink: 0;
      justify-content: space-between;
      align-items: center;
      img {
        margin-right: 10px;
        width: 18px;
      }
      span {
        margin-left: 10px;
        background: #fff;
        color: #790E8B;
        font-size: 12px;
        border-radius: 50%;
        display: inline-flex;
        width: 22px;
        height: 22px;
        justify-content: center;
        align-items: center;
      }
      &:hover {
        background-color: #b363c1 !important;
        color:#fff!important;
      }
    }
    &--add {
      /*padding-left: 61px;*/
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 31px;
      padding-right: 21px;
      width: 100%;
      &:after {
        content:'+';
        display: flex;
        justify-content: center;
        align-items: center;
        color: #AB47BC;
        width: 28px;
        height: 28px;
        background: #fff;
        border-radius: 50%;
        font-size: 20px;
        line-height: 28px;
        padding-bottom: 3px;
      }
      &:hover {
        background-color: #b363c1 !important;
        color:#fff!important;
      }
    }
  }
  &__header {
    &__notice {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 23px 35px;
      gap: 42px;
      background: #F6F7F8;
      border-radius: 16px;
      width: 100%;
      margin-bottom: 40px;
      justify-content: space-between;
      &--authorized {
        background: transparent;
        padding: 0;
      }
    }
  }
  .woocommerce-result-count {
    display: none;
  }
  .product {
    transition: 0.3s ease;
    &:hover {
      box-shadow: 0px 11px 16px rgba(0, 0, 0, 0.15);
      transform: translateY(-10px);
    }
  }
  .products__addsystem {
    margin-top: 32px;
    padding: 20px;
    background: #F6F7F8;
    border-radius: 16px;
    p{
      margin-bottom: 20px;
      font-size: 18px;
      line-height: 145%;
      color: #667085;
    }
  }
   nav.woocommerce-pagination {
     display: none;
   }
}
.products__right {
  &.loader {
    position: relative;
    &::after {
      content: '';
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      left: 0;
      right: 0;
      top:0;
      bottom:0;
      background-color: rgba(255, 255, 255, 0.8);
    }
    &:before {
      display: none!important;
    }
  }
}
#loadedElements {
  display: flex;
  justify-content: center;
  .catalogList__loadmore__btn {
    display: flex;
    align-items: center;
    &:after {
      content:'';
      display: inline-block;
      width: 12px;
      height: 8px;
      background-image: url("../images/components/arrow-down.svg");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      margin-left: 7px;
    }
  }
}

.products__category-list li {
  span {
    cursor: pointer;
    display: block;
    padding: 10px 15px;
    border-left: 1px solid #e4e7ec;
    color: #98a2b3 !important;
    &:hover {
      background: transparent !important;
      border-color: #1d2939;
      color: #1d2939 !important;
    }

  }
  &.reset-filter span {
    color: #ab47bc!important;
  }
}